<template>
    <main class="app-main">
        <p class="title">登录</p>
<!--        <md-button type="primary" @click="login">登录</md-button>-->
        <form class="login-form">
            <p class="tip01">欢迎登陆</p>
            <p class="tip02">北京市军民科技协同创新平台</p>
            <div class="input-cover">
                <span class="username"></span>
                <span class="line"></span>
                <input class="loginInput" type="text" v-model="loginForm.username" placeholder="请输入邮箱用户名" @blur.prevent="inputLoseFocus">
            </div>
            <div class="input-cover">
                <span class="password"></span>
                <span class="line"></span>
                <input class="loginInput" type="password" v-model="loginForm.password" placeholder="请输入密码" @blur.prevent="inputLoseFocus">
            </div>
            <div class="remeber-password">
                <div class="left">
                    <div class="checkbox-cover" :class="{'is-active': isRemeberPassword}">
                        <input type="checkbox" id="remeber-check" @click="toggleRemeberStatus">
                    </div>
                    <label for="remeber-check" @click="toggleRemeberStatus">记住密码</label>
                </div>
                <span class="forget-password" href="" @click="turnToResetPsw">忘记密码?</span>
            </div>
            <div class="login-btn">
                <input type="button" @click="login" value="登录">
            </div>
            <div class="register">
                <p>还没有账号,去<span @click="turnToRegister">注册?</span></p>
            </div>
        </form>
    </main>
</template>

<script>
import { Button, InputItem, NumberKeyboard, Field, Icon, Toast } from 'mand-mobile'
export default {
  name: 'Login',
  components: {
    [Button.name]: Button,
    [InputItem.name]: InputItem,
    [NumberKeyboard.name]: NumberKeyboard,
    [Field.name]: Field,
    [Icon.name]: Icon,
    [Toast.name]: Toast
  },
  data () {
    return {
      loginForm: {
        username: '',
        password: ''
      },
      isRemeberPassword: false
    }
  },
  methods: {
    login () {
      if (!this.loginForm.username) {
        Toast.failed('请输入邮箱用户名')
        return
      }
      if (!this.loginForm.password) {
        Toast.failed('请输入密码')
        return
      }
      this.$store.dispatch('Login', this.loginForm).then(() => {
        this.$router.push('/personal')
      }).catch(err => {
        Toast.failed(err)
      })
    },
    toggleRemeberStatus () {
      this.isRemeberPassword = !this.isRemeberPassword
    },
    turnToResetPsw () {
      this.$router.push({
        path: '/resetPsw'
      })
    },
    turnToRegister () {
      this.$router.push({
        path: '/register'
      })
    },
    inputLoseFocus () {
      setTimeout(() => {
        window.scrollTo(0, 0)
      }, 100)
    }
  }
}
</script>

<style lang="stylus" scoped>
    .app-main
        margin-bottom 0!important
        height 100%
        background url("../../assets/login/login_bg@2x.png") no-repeat
        background-size 100% 100%
        .title
            height 100px
            line-height 100px
            font-size 36px
            color #ffffff
            border-bottom 1px solid rgba(255,255,255,0.2)
        .login-form
            width 630px
            height 770px
            margin 121px auto 0
            p
                color #ffffff
                font-family PingFang-SC-Regular
                text-align left
                width 553px
                margin 0 auto 30px
            .tip01
                font-size 32px
                margin 0 auto 30px
            .tip02
                font-size 42px
                margin-bottom 120px
            .input-cover
                width 630px
                height 88px
                font-size 28px
                border-radius 44px
                background rgba(255,255,255,0.08);
                margin-bottom 50px
                display flex
                align-items center
                span.username
                    height 40px
                    line-height normal
                    display inline-block
                    width 80px
                    background url("../../assets/login/login_icon_phone@2x.png") no-repeat 39px center
                    background-size 22px 30px
                span.password
                    height 40px
                    line-height normal
                    display inline-block
                    width 80px
                    background url("../../assets/login/login_icon_code@2x.png") no-repeat 39px center
                    background-size 22px 30px
                span.line
                    width 1px
                    display inline-block
                    border-left 1px solid #ffffff
                    height 40px
                    margin-left 20px
                    margin-right 20px
                    opacity 0.2
                input
                    background-color transparent
                    border 0
                    outline none
                    color #ffffff
                    width 100%
                input::-webkit-input-placeholder
                    color rgba(255,255,255,0.5)
                input:-webkit-autofill
                    -webkit-text-fill-color #ededed !important
                    -webkit-box-shadow 0 0 0px 1000px transparent  inset !important
                    background-color transparent
                    background-image none
                    transition background-color 50000s ease-in-out 0s
            .remeber-password
                width 564px
                margin 0 auto
                display flex
                justify-content space-between
                font-size 24px
                color #ffffff
                align-items end
                .checkbox-cover
                    width 26px
                    height 26px
                    border 1px solid rgba(255,255,255,0.3)
                    margin-right 8px
                .is-active
                    background transparent url("../../assets/login/icon_duigou@2x.png") no-repeat
                    background-size 100% 100%
                #remeber-check
                    opacity 0
                .left
                    display: flex
                    justify-content space-between
                .forget-password
                    background linear-gradient(45deg,rgba(202,2,57,1) 0%, rgba(255,97,53,1) 100%);
                    -webkit-background-clip text
                    -webkit-text-fill-color transparent
            .login-btn
                margin-bottom 50px
                input
                    width 630px
                    height 88px
                    background rgba(2,25,85,1)
                    border-radius 12px
                    color #ffffff
                    font-size 32px
                    border 0
            .register
                font-size 24px
                color #ffffff
                p
                    text-align center
                span
                    background linear-gradient(45deg,rgba(202,2,57,1) 0%, rgba(255,97,53,1) 100%);
                    -webkit-background-clip text
                    -webkit-text-fill-color transparent
</style>
